.ui-navbar {
  width: 30px;
  display: flex;
  align-items: center;
  // background-color: skyblue;
  // box-shadow: 1px 1px 8px 0 rgba(0, 0, 0, 0.2);
  .nav {
    // position: absolute;
    background-color: #ffffff;
    padding-top: 16px;
    padding-bottom: 16px;
    // z-index: 1;
    padding-left: 10px;
    padding-right: 10px;
    ul {
      list-style: none;
      transition: 0.5s all;
      li {
        overflow: hidden;
        .line {
          height: 1px;
          width: 10px;
          display: inline-block;
          background-color: #343a40;
        }
        &.active {
          // color: #0077cc;
          // .line {
          //   background-color: #0077cc;
          // }
        }
        &:hover {
          cursor: pointer;
          filter: blur(0.8px);
          transition: 0.3s all;
        }
      }
    }
    &.fold {
      background-color: transparent;
      opacity: 0.7;
      transition: 0.5s all;
      ul {
        width: 10px;
        li {
        }
      }
    }
    &.unfold {
      opacity: 0.9;
      -webkit-box-shadow: 7px 4px 13px 6px #c7c7c7;
      box-shadow: 7px 4px 13px 6px #c7c7c7;
      // transition: 1s opacity ease-in-out;
      transition: 0.5s all;
      ul {
        width: 100px;
        padding-left: 10px;
        text-transform: uppercase;
        font-weight: bold;
        color: #242729;
      }
    }
  }
}
