    width: 100%;
    background-color: #fafbfc;
  }
  .pagehead-tabs-component {
    width: 100%;
    background-color: #fafbfc;
  }
  .pinned-repos-component {
    h3 {
      color: #666;
      font-weight: 400;
    }
    ol {
      color: #666;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        width: 319px;
        // height: 100px;
        // float: left;
        // margin-right: 11px;
        display: flex;
      }
    }
    .private {
      padding-top: 30px;
    }
  }
  .org-repos-component {
  }
  .loading {
    background-image: url('../assets/images/loading.gif');
    background-repeat: no-repeat;
    width: 170px;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    z-index: 100;
    height: 200px;
  }
}

