.ui-note {
  .notes {
    // list-style-type: none;
    // display: flex;
    align-content: flex-start;
    flex-flow: row wrap;
    .note {
      max-width: 300px;
      min-width: 200px;
      min-height: 100px;
      max-height: 450px;
      margin: 20px;
      padding: 20px;
      -webkit-box-shadow: 4px 4px 15px 8px #c2c2c2;
      box-shadow: 4px 4px 15px 8px #c2c2c2;
      border-radius: 5px;
      display: flex;
      justify-content: space-between;
      float: left;
      .date {
        width: 10px;
        writing-mode: vertical-lr;
        transform: rotate(180deg);
        text-align: center;
        background-color: aliceblue;
      }
      .text {
        width: calc(100vw - 30px);
        padding-left: 20px;
        .tags {
          span {
            // border: 1px solid white;
            background-color: white;
            border-radius: 5px;
            padding-left: 3px;
            padding-right: 3px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 640px) {
  .ui-note {
    display: inline-block;
    .notes {
      display: flex;
      justify-content: center;
    }
  }
}
