.ui-history {
  .ui-companys {
    ul {
      color: #343a40;
      li {
        list-style: none;
        margin-bottom: 20px;
      }
    }
  }
  .ui-projects {
    // margin-left: 100px;
    div {
      color: #343a40;
    }
    .years {
      /*color:#1DB702;*/
      display: inline-block;
      width: 100%;
    }
    .logo {
      border-radius: 25px;
      float: left;
    }
    .pline {
      display: inline-block;
      .line {
        margin-left: 70px;
        li {
          min-height: 100px;
          max-height: 500px;
          width: 100%;
          float: left;
          list-style: none;
          border-left-width: 1px;
          border-left-style: dotted;
          padding-top: 15px;
          padding-left: 10px;
          .tags {
            word-break: break-all;
            font-size: 12px;
            .tag {
              min-width: 20px;
              max-width: 200px;
              height: 25px;
              margin: 5px 10px 5px 0;
              padding: 2px 5px 2px 5px;
              border-radius: 3px;
              background-color: silver;
              cursor: pointer;
              float: left;
            }
          }
        }
      }
    }
    .date {
      margin-top: -23px;
      margin-left: -80px;
      font-size: 8pt;
      font-weight: 600;
      height: 60px;
      width: 65px;
      padding-right: 5px;
      word-break: break-word;
      text-align: right;
    }

    .circle {
      width: 8px;
      height: 8px;
      background-color: white;
      border-radius: 3px;
      border-width: 1px;
      border-style: solid;
      margin-left: -14px;
      margin-top: -24px;
    }

    .link {
      margin-right: 10px;
      color: #0077cc;
    }

    .p-discribe {
      font-size: 12px;
    }
  }
}
