.ui-release {
  width: 90%;
  margin: auto;
  .release-log {
    display: flex;
    .sidemenu {
      width: 110px;
    }
    .content {
      width: calc(100vw - 110px);
    }
  }
}

@media screen and (max-width: 640px) {
  .ui-release {
    .release-log {
      display: block;
      .sidemenu {
        width: 100%;
      }
      .content {
        width: 100%;
      }
    }
  }
}
