.ui-road {
  height: calc(100vh - 53px);
  width: 95%;
  overflow-y: scroll;
  // overflow-y: hidden;
  svg {
    cursor: pointer;
    &.icon-refresh-theme {
      animation: circle 1.5s infinite linear;
      &:hover {
        animation: none;
      }
    }
    @keyframes circle {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }
}
